.sectionContainer {
    background-color: #0c1d37;
    border-radius: 50px;
    padding: 48px;
    width: 65%;

}

:selection {
    background: #b3d4fc;
    text-shadow: none;
}

h3 {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}

.contactContainer h3 {
    letter-spacing: 1px;
    margin-bottom: 24px;
    color: white;
    font-size: 1.5rem;
}

.contactTypes {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    text-align: center;
}

.contactFormContainer {
    margin-top: 48px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;

}

.contactTypes .flexContainer {
    -webkit-box-flex: 0;
    -ms-flex: 0 0 50%;
    flex: 0 0 50%;
    max-width: 50%;
}

.contactTypes .flexContainer:first-child {
    padding: 0 15px 0 0;
}

.contactTypes .flexContainer:last-child {
    padding: 0 0 0 15px;
}



.contactFormContainer form {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    justify-content: center;

}

button {
    font-size: 100%;
    margin: 0;
    vertical-align: baseline;
    line-height: normal;
    text-transform: none;
    /* -webkit-appearance: button; */
    cursor: pointer;
}

button {
    color: #222;
}

.contactFormContainer button {
    padding: 14px 40px;
    font-size: 0.9rem;
    border: none;
    background-color: #ff6501;
    color: white;
    letter-spacing: 1px;
    border-radius: 40px;
    width: 140px;
    margin: 0 auto;
    -webkit-transition: all 0.3s ease-in;
    transition: all 0.3s ease-in;
}

.contactFormContainer button:hover {
    -webkit-box-shadow: 0px 10px 40px 0px rgba(0, 0, 0, 0.2);
    box-shadow: 0px 10px 40px 0px rgba(0, 0, 0, 0.2);
}

.contactTypes .flexContainer .typeBox {
    -webkit-box-shadow: 0px 4px 8px rgb(66 93 167 / 56%);
    box-shadow: 0px 14px 8px rgba(134, 151, 168, 0.1);
    -webkit-transition: all 0.3s ease-in;
    transition: all 0.3s ease-in;
    padding: 30px;
    border-radius: 15px;
}

@media (min-width: 768px) {
    .contactTypes .flexContainer .typeBox:hover {
        -webkit-box-shadow: 0px 14px 28px rgba(134, 151, 168, 0.08);
        box-shadow: 0px 14px 28px rgba(134, 151, 168, 0.08);
    }
}

label {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}

.srOnly {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    -webkit-clip-path: inset(100%);
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
}

.contactFormContainer form input {
    -webkit-transition: all 0.3s ease-in;
    transition: all 0.3s ease-in;
    font-family: 'Poppins', sans-serif;
    letter-spacing: 1px;
    background-color: hsla(0, 0%, 100%, .1);
    border: 1px solid hsla(0, 0%, 100%, 0);
    border-radius: 50px;
    color: #fff;
    font-size: 18px;
    height: 35px;
    line-height: 120%;
    margin-bottom: 20px;
    padding: 20px 26px;
    width: 300px;
    display: grid;
    grid-template-rows: repeat(2, 1fr);
    gap: 5px;
}

.input1 {
    display: grid;
    gap: 40px;
}

.input2 {
    display: grid;
    gap: 40px;
    margin-left: 20px;
}

.contactFormContainer form .subjectInput {
    -ms-grid-column: 1;
    -ms-grid-column-span: 2;
    grid-column: 1 /3;
}

textarea {
    overflow: auto;
    vertical-align: top;
    resize: vertical;
}

textarea {
    font-size: 100%;
    margin: 0;
    vertical-align: baseline;
}

textarea {
    overflow: auto;
    vertical-align: top;
}

textarea {
    color: #222;
}

textarea {
    resize: vertical;
}

.contactFormContainer form textarea {
    border: 1px solid #eee;
    -webkit-transition: all 0.3s ease-in;
    transition: all 0.3s ease-in;
    padding: 11px 20px;
    font-size: 1rem;
    font-family: 'Poppins', sans-serif;
    letter-spacing: 1px;
    width: 100%;
}

.contactFormContainer form textarea {
    resize: none;
}

.contactFormContainer form .messageInput {
    -ms-grid-column: 1;
    -ms-grid-column-span: 2;
    grid-column: 1 /3;
}

