.box {
    align-self: flex-end;
    animation-duration: 10s;
    animation-iteration-count: infinite;
    /* background-color: #F44336; */
    /* height: 200px; */
    margin: 0 auto 0 auto;
    transform-origin: bottom;
    /* width: 200px; */
}
.bounce-1 {
    animation-name: bounce-1;
    animation-timing-function: linear;
}
@keyframes bounce-1 {
    0%   { transform: translateY(0); }
    50%  { transform: translateY(-100px); }
    100% { transform: translateY(0); }
}


.block-hero{
    margin-top: 170px;
}
.block-hero-img {
    z-index: -1;
    position: relative;
    
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    margin-right: 80%;
    margin-top: 80px;
    /* margin-left: 20px; */
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-pack: start;
    -webkit-justify-content: flex-start;
    -ms-flex-pack: start;
    justify-content: flex-start;
    -webkit-box-align: center;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center;
}

.block-hero {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center;
    text-align: center;
}

h1 {
    font-size: 60px;
    margin: .67em 0;
    font-weight: 700;
    margin-bottom: 28px;
    line-height: 122%;
    margin-top: 0px;
    color: #0c1d37;
    letter-spacing: -1px;
}

.heading-hero {
    max-width: 1100px;
}

p {
    margin-top: 0;
    margin-bottom: 28px;
}

.paragraph-hero {
    max-width: 500px;
    margin-bottom: 34px;
}

.w-form {
    margin: 0 0 15px;
}

.form-block-banner {
    max-width: 420px;
}

.form-block-banner.herocta {
    max-width: 100%;
}


.block-circle---hero-1 {
    position: absolute;
    z-index: 5;
    width: 80%;
}

.block-circle---hero-2 {
    position: absolute;
    left: auto;
    top: auto;
    right: 20px;
    bottom: -40px;
    z-index: 15;
    width: 26%;
}

.text-accent {
    display: inline-block;
    padding-right: 0.25em;
    padding-left: 0.25em;
    border-radius: 8px;
    background-color: rgba(255, 101, 1, 0.12);
    color: #ff6501;
}

.form-banner {
    display: -ms-grid;
    display: grid;
    grid-auto-columns: 1fr;
    grid-column-gap: 9px;
    grid-row-gap: 9px;
    -ms-grid-columns: 1fr auto;
    grid-template-columns: 1fr auto;
    -ms-grid-rows: auto;
    grid-template-rows: auto;
}

.w-form-done {
    display: none;
    padding: 20px;
    text-align: center;
    background-color: #dddddd;
}

.w-form-fail {
    display: none;
    margin-top: 10px;
    padding: 10px;
    background-color: #ffdede;
}

