input {
    color: inherit;
    font: inherit;
    margin: 0;
    line-height: normal;
}

.w-input {
    display: block;
    width: 100%;
    height: 38px;
    padding: 8px 12px;
    margin-bottom: 10px;
    font-size: 14px;
    line-height: 1.42857143;
    color: #333333;
    vertical-align: middle;
    background-color: #ffffff;
    border: 1px solid #cccccc;
}

.text-field-white {
    height: 56px;
    margin-bottom: 0px;
    padding: 14px 26px;
    border-style: solid;
    border-width: 1px;
    border-color: hsla(0, 0%, 100%, 0);
    border-radius: 50px;
    background-color: hsla(0, 0%, 100%, 0.1);
    -webkit-transition: border-color 200ms ease;
    transition: border-color 200ms ease;
    color: #fff;
    font-size: 18px;
    line-height: 120%;
}

.text-field-white.tophero {
    border-style: solid;
    border-color: #0c1d37;
    background-color: #0c1d37;
    color: #fff;
}

.w-button {
    display: inline-block;
    padding: 9px 15px;
    background-color: #3898EC;
    color: white;
    border: 0;
    line-height: inherit;
    text-decoration: none;
    cursor: pointer;
    border-radius: 0;
}

.submit-button-white {
    padding: 18px 40px;
    border-radius: 50px;
    background-color: #fff;
    -webkit-transition: color 200ms ease, background-color 200ms ease;
    transition: color 200ms ease, background-color 200ms ease;
    color: #0c1d37;
    font-size: 18px;
    line-height: 115%;
    font-weight: 500;
}

input.w-button {
    -webkit-appearance: button;
}

.submit-button-white.herobtn {
    background-color: #ff6501;
}

.success-text {
    color: #12b361;
    line-height: 140%;
}

.error-text {
    color: #ff6501;
    line-height: 140%;
}


